@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap";
:root {
  --background-color-main: #0e0e0e;
  --font-cards: "Montserrat", sans-serif;
  --color-title-cards: #028b09;
}

ul {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

main {
  height: auto;
  background-image: url("background-image-main.132dd068.png");
  background-color: var(--background-color-main);
  padding: 20px;
}

.row {
  grid-template-columns: repeat(2, 135px);
  justify-content: center;
  gap: 32px 20px;
  display: grid;
}

.event {
  width: 135px;
  height: fit-content;
  border-radius: 35% 5px;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 10px 0;
  display: flex;
}

.event:hover {
  transition: transform .3s, box-shadow .3s;
  transform: scale(1.2);
  box-shadow: 0 0 20px #028b09;
}

.img-card {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 50px 2px;
  margin-top: 10px;
  margin-right: 10px;
}

.title-event {
  width: 130px;
  font-family: var(--font-cards);
  letter-spacing: 0;
  color: var(--color-title-cards);
  text-align: center;
  margin: 0;
  font-size: 14px;
}

.data-event {
  font-family: var(--font-cards);
  color: #fff;
  text-align: center;
  width: 130px;
  margin: 0;
  font-size: 14px;
}

.location-event {
  font-family: var(--font-cards);
  text-align: center;
  color: #fff;
  margin: 0 12px;
  font-size: 14px;
  font-weight: bold;
}

#pagination-container {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 60px 0;
  padding: 60px 47px;
  display: flex;
}

.page-number {
  width: 30px;
  height: 30px;
  color: #fff;
  background-color: var(--background-color-main);
  letter-spacing: 0;
  text-align: center;
  border: 0;
  border-radius: 50%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  transition: background-color .3s;
  display: flex;
}

.page-number:hover {
  cursor: pointer;
  background: #028b09;
  border-radius: 50%;
}

#active {
  background: #028b09;
  border-radius: 50%;
}

@media screen and (min-width: 768px) {
  main {
    padding: 32px;
  }

  .row {
    grid-template-columns: repeat(4, 152px);
    justify-content: center;
    gap: 40px 32px;
    display: grid;
  }

  .event {
    width: 152px;
    gap: 8px;
    padding: 10px 0;
  }

  .img-card {
    width: 141px;
    height: 120px;
    border-radius: 50% 5px;
  }

  .title-event {
    font-size: 16px;
    font-weight: 700;
    line-height: 19.5px;
  }

  .data-event {
    font-size: 16px;
    font-weight: 400;
  }
}

@media screen and (min-width: 1158px) {
  main {
    padding: 0 16px 60px;
  }

  .row {
    grid-template-columns: repeat(4, 288px);
    justify-content: center;
    gap: 40px 32px;
    display: grid;
  }

  .event {
    width: 288px;
    gap: 8px;
    padding: 10px 0;
  }

  .img-card {
    width: 267px;
    height: 226px;
    border-radius: 39% 5px;
  }

  .title-event {
    font-size: 16px;
    font-weight: 700;
    line-height: 19.5px;
  }

  .data-event {
    font-size: 16px;
    font-weight: 400;
  }
}

